*, body {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  font-weight: 400;
}

a {
  color: #4aaea5;
  text-decoration: none;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
a:hover {
  color: #56dbd1;
}

/********************************* COMMON *********************************/
.b_hidden {
  opacity: 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.b_unselectable {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}

/********************************* TEXT ***********************************/
.text {
  font-size: 16px;
  color: #ffffff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.text_hover:hover {
  color: #56dbd1;
  cursor: pointer;
}
.text_main {
  color: #56dbd1;
}
.text_muted {
  color: #989898;
}
.text_fancy {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.text_fancy:before, .text_fancy:after {
  content: "";
  border-top: 2px solid;
  margin: 0 20px 0 0;
  flex: 1 0 20px;
}
.text_fancy:after {
  margin: 0 0 0 20px;
}

.text-block + .text-block {
  margin-top: 20px;
}
.text-block__paragraph {
  text-align: left;
  justify-content: flex-start;
}
.text-block__paragraph + .text-block__paragraph {
  margin-top: 10px;
}

.subtext {
  font-size: 14px;
  color: #989898;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.header {
  font-size: 20px;
  color: #ffffff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

/********************************* BLOCK **********************************/
.block {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}
.block_expanded {
  padding: 20px;
}

/********************************* BUTTON *********************************/
.btn {
  color: #ffffff;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 15px;
  background-color: #4aaea5;
  border: none;
  border-radius: 15px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.btn:hover {
  cursor: pointer;
  background-color: #3f8075;
}
.btn + .btn {
  margin-left: 10px;
}
.btn_inline {
  background-color: transparent;
}
.btn_inline:hover {
  background-color: transparent;
  color: #f5f5f5;
}

/********************************* LOGO *********************************/
.logo {
  width: 10rem;
  height: 10rem;
  opacity: 0.7;
  object-fit: contain;
  -webkit-transition: 1s;
  transition: 1s;
}
.logo:hover {
  opacity: 1;
  cursor: pointer;
}

